.auth-wrapper {
  display: flex;
  min-height: 100vh;
  font-family: 'Poppins', sans-serif;
  background-color: #f0f4f8;
}

.auth-left {
  flex: 1;
  background: linear-gradient(135deg, #43cea2, #185a9d);
  ;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 50px;
  text-align: center;
  position: relative;
  overflow: hidden;
  clip-path: polygon(0 0, 90% 0, 110% 100%, 0% 100%);
}

.auth-left .logo {
  width: 150px;
  margin-bottom: 30px;
}

.auth-left h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.auth-left p {
  font-size: 1.1rem;
  line-height: 1.6;
}

.auth-left p,
.auth-left h1 {
  max-width: 500px;
  word-wrap: break-word;
}


/* Right Side */
.auth-right {
  flex: 1;
  background: #f0f4f8;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px;
  position: relative;
  z-index: 1;
}

.auth-right::before {
  content: '';
  position: absolute;
  top: 0;
  left: -50px;
  width: 100px;
  height: 100%;
  background: #f0f4f8;
  transform: skewX(-25deg);
  z-index: -1;
}

/* Form Card */
.auth-form {
  width: 100%;
  max-width: 400px;
  background: #fff;
  padding: 45px 35px;
  border-radius: 15px;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.auth-form:hover {
  transform: translateY(-5px);
  box-shadow: 0 25px 45px rgba(0, 0, 0, 0.2);
}

.auth-form h2 {
  text-align: center;
  margin-bottom: 30px;
}

.input-group {
  position: relative;
  margin-bottom: 25px;
}

.input-group input {
  width: 100%;
  padding: 12px 15px;
  border: 1px solid #ccc;
  border-radius: 8px;
  outline: none;
  font-size: 1rem;
}

.input-group label {
  position: absolute;
  top: -8px;
  left: 12px;
  background: #fff;
  padding: 0 5px;
  font-size: 0.85rem;
  color: #555;
}

.password-group i {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  color: #666;
}

.btns {
  width: 100%;
  padding: 12px;
  background: linear-gradient(135deg, #43cea2, #185a9d);
  color: #fff;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 10px;
  transition: 0.3s;
}

.btns:hover {
  opacity: 0.9;
  transform: translateY(-2px);
}

.switch-form {
  text-align: center;
  margin-top: 20px;
}

.switch-form span {
  color: #2575fc;
  cursor: pointer;
  font-weight: 600;
}


@media (max-width: 900px) {
  .auth-wrapper {
    flex-direction: column;
    min-height: 100vh;
  }

  /* Top Banner (was left side) */
  .auth-left {
    width: 100%;
    padding: 60px 20px;
    text-align: center;
    border-bottom-left-radius: 40px;
    border-bottom-right-radius: 40px;
    clip-path: none;
    /* remove polygon */
    color: #fff;
  }

  .auth-left .logo {
    width: 100px;
    margin-bottom: 20px;
  }

  .auth-left h1 {
    font-size: 2rem;
    margin-bottom: 15px;
  }

  .auth-left p {
    font-size: 1rem;
    line-height: 1.5;
    max-width: 90%;
    margin: 0 auto;
  }

  /* Form Section */
  .auth-right {
    width: 100%;
    padding: 30px 20px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    background: transparent;
  }

  .auth-form {
    width: 100%;
    max-width: 380px;
    padding: 35px 25px;
    border-radius: 20px;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
  }

  .auth-form h2 {
    font-size: 1.8rem;
    margin-bottom: 25px;
    text-align: center;
  }

  .input-group input {
    padding: 10px 12px;
    font-size: 0.95rem;
  }

  .btn {
    padding: 12px;
    font-size: 1rem;
    border-radius: 12px;
  }

  .switch-form {
    margin-top: 15px;
  }
}

/* Extra small screens (phones) */
@media (max-width: 500px) {
  .auth-left {
    padding: 50px 15px;
  }

  .auth-left h1 {
    font-size: 1.6rem;
  }

  .auth-left p {
    font-size: 0.95rem;
  }

  .auth-form {
    padding: 25px 20px;
  }

  .auth-form h2 {
    font-size: 1.5rem;
  }

  .input-group input {
    padding: 8px 10px;
    font-size: 0.9rem;
  }

  .btn {
    padding: 10px;
    font-size: 0.95rem;
  }
}